.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDekstop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;
    margin-bottom: 28px;
    transition: transform 0.5s;

    @include onDekstop {
      margin-bottom: 32px;
    }

    &:hover {
      transform: scale(1.1);
    }
  }

  &__photo {
    grid-column: 2/4;
    width: 100%;
    border-radius: 50%;
  }

  &__name {
    margin: 0 0 4px;

    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: $color-blue;
  }

  &__role {
    margin: 0 0 4px;

    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }

  &__about {
    text-align: center;
  }
}
